import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaLineA = null;
export default {
	data() {
		return {
			navTitles: ['血压', '血氧', '血糖'],
			currentIndex: 0,
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,

			this_week: '6月7日-6月13日',
			avg_val_blood: '135/80',
			avg_val_time: 65,
		};
	},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		// 加载页面渲染血压的图表信息
		this.getServerData(1);
	},
	methods: {
		switchTab(index) {
			this.currentIndex = index
			this.getServerData(this.currentIndex + 1)
		},
		getServerData(canvasId) {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					// console.log(res.data.data)
					let LineA = {
						categories: [],
						series: []
					};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					LineA.categories = res.data.data.LineA.categories;
					// LineA.series = res.data.data.LineA.series;
					// LineA.series = res.data.data.LineA.series;
					// LineA.series = [LineA.series.pop()];
					// console.log(LineA.series)
					LineA.series = [{
						data: [0, 100, 70, 20, 100, 50],
						name: 'aa'
					}]
					_self.showLineA(canvasId, LineA);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showLineA(canvasId, chartData) {
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				fontSize: 11,
				colors: ['#24C789'],
				legend: {
					show: true
				},
				dataLabel: false,
				dataPointShape: true,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					disableGrid: true
				},
				yAxis: {
					data: [{
						axisLine: false,
					}],
					gridType: 'dash',
					gridColor: '#CCC',
					dashLength: 2,
					min: 0.00,
					max: 150.00,
					format: (val) => {
						return val.toFixed(0.00)
					}
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'curve'
					}
				}
			});
		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		}
	},
}
